/* Set up the hero section to be full viewport height */
.hero-section {
    position: relative;
    height: 400px;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    color: #fff;
    overflow: hidden;
  }
  
  /* Ensure the video covers the entire section */
  .hero-video {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    z-index: -1;
  }
  
  /* Styling for the content over the video */
  .hero-content {
    z-index: 2;
  }
  
  /* Brand name styles */
  .hero-content h1 {
    font-size: 3rem;
    margin-bottom: 0.5rem;
  }
  
  /* Slogan styles */
  .hero-content p {
    font-size: 1.5rem;
    margin-bottom: 1rem;
  }
  
  /* Button styles */
  .hero-button {
    padding: 12px 25px;
    font-size: 1.2rem;
    color: #fff;
    background: linear-gradient(45deg, #007bff, #6610f2);
    border: none;
    border-radius: 30px; /* Rounded edges */
    cursor: pointer;
    transition: all 0.3s ease;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
    text-transform: uppercase; /* Makes text uppercase */
    letter-spacing: 1px; /* Adds spacing between letters */
  }
  
  .hero-button:hover, .hero-button:focus {
    background: linear-gradient(45deg, #6610f2, #007bff);
    box-shadow: 0 6px 12px rgba(0, 0, 0, 0.3);
    transform: translateY(-2px); /* Slight raise effect */
  }
  
  /* Responsive styling */
  @media (max-width: 768px) {
    .hero-content h1 {
      font-size: 2rem;
    }
  
    .hero-content p {
      font-size: 1rem;
    }
  
    .hero-button {
      font-size: 1rem;
      padding: 10px 20px;
    }
  }